import {Component, OnInit, Input, Output} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {EntityService} from '../entity.service';
import {BookType, Book} from '../entity';
@Component({
  selector: 'app-update-book',
  templateUrl: './update-book.component.html',
})
export class UpdateBookComponent implements OnInit {
  /**/
  @Input() bookTypes: BookType[];
  book: Book;
  constructor(
    private route: ActivatedRoute,
    private bookService: EntityService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getBookTypes(); // 
    const id = this.route.snapshot.params.id; // id
    this.bookService.getBook(id)
      .subscribe(res => {
        this.book = res;
      });
  }
  /**/
  compareFn(o1: BookType, o2: BookType): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  getBookTypes(): void {
    this.bookService.getBookTypes()
      .subscribe(res => this.bookTypes = res);
  }
  save(): void { // 
    /**/
    console.log(this.book);
    this.bookService.updateBook(this.book)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
}
